import React, {useState, useRef} from 'react'
import {motion} from 'framer-motion'

    // import Header from './Header'
import Navbar from './Navbar'
import Landing from './Landing'
import GetTo from './GetTo'
import SomeOf from './SomeOf'
import WhatI from './WhatI'
import WhatI2 from './WhatI2'
import Contact from './Contact'


function All() {
    const [all, setAll] = useState("0")
    const [top, setTop] = useState("0")
    const [prog, setProg] = useState("0")

    // const all = document.querySelector("#All").offsetHeight
    console.log(all)
    setTimeout(() => {
        setAll(document.querySelector("#All").offsetHeight)
        
        
        // [all] 5000        100 [%]
        // [top] 2500        x  [prog] 
        
    }, 500);
    // function handleEvents(){
    //     var supportPageOffset = window.pageXOffset !== undefined;
    //     var isCSS1Compat = ((document.compatMode || "") === "CSS1Compat");

    //     // const All = document.getElementById("All")
    //     var y = supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop;
    //     console.log("top :", y)
    //     setTop(y)

    // }
    function handleScroll(){
        var supportPageOffset = window.pageXOffset !== undefined;
        var isCSS1Compat = ((document.compatMode || "") === "CSS1Compat");

        // const All = document.getElementById("All")
        var y = supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop;
        console.log('y :',y)
        setTop(y)

        const one = all / 6
        const prog = document.getElementById("Progress")

        if(y < one * 1){
            console.log("one")
            // prog.style.height = `16.6%`
            progr(`16.6%`)
        } else if(y > one * 1 && y < one * 2){
            console.log("two")
            // prog.style.height = `33.33%`
            progr(`33.33%`)
        } else if(y > one * 2 && y < one * 3){
            console.log("three")
            // prog.style.height = `49%`
            progr(`49%`)
        }else if(y > one * 3 && y < one * 4){
            console.log("four")
            // prog.style.height = `66.66%`
            progr(`66.66%`)
        } else if(y > one * 4 && y < one * 5){
            console.log("five")
            // prog.style.height = `83%`
            progr(`83%`)
        }else if(y > one * 5 && y < one * 6){
            console.log("six")
            // prog.style.height = `100%`
            progr(`100%`)
        } 
    }
    function progr(value) {
        const prog = document.getElementById("Progress")
        if(document.querySelector("#All").offsetWidth < 1201){
            console.log("mobile")
            prog.style.width = value
            prog.style.height = "2px"
        }
        else {
            console.log("lap")
            prog.style.height = value
            prog.style.width = "2px"
        }
    }
    const refCur = useRef()
    setTimeout(() => {
        
        const cursor = document.getElementById("Cursor")
        document.addEventListener('mousemove', (e) =>{
            cursor.style.left =  e.clientX-10 + 'px';
            cursor.style.top = e.clientY + 'px';
        })
    }, 200);


    return (
        <motion.div onWheel={handleScroll} className="all" id="All" >
            {/* <Header /> */}
            <Navbar />
            <div  className="top">
                <div className="back"></div>
                <Landing/>
                <GetTo />
                <SomeOf />
                <WhatI />
                <WhatI2 />
                <Contact />
            </div>
            <div className="cursor" id='Cursor' ref={refCur}></div>
        </motion.div>
    )
}

export default All
