import React, {useState, useEffect} from 'react'
import { motion } from "framer-motion"


import Linkss from './common/Linkss'
import Href from './common/Href';


function Navbar() {
    const [all, setAll] = useState("0")
    const [top, setTop] = useState("0")
    const [state, setstate] = useState(true)
    const [flipFlop, setflipFlop] = useState(true)
    const [animate, setanimate] = useState({x:0, opacity: 1, transition:{ duration:".5", transitionTimingFunction: "easeOutBack"}})
    
    const [clicks, setClicks] = useState(
        [
            {id: 0, href: "Home", statues : true},
            {id: 1, href: "About", statues : false},
            {id: 2, href: "Porfolio", statues : false},
            {id: 3, href: "Services", statues : false},
            {id: 4, href: "resources", statues : false},
            {id: 5, href: "Contact", statues : false},
        ]
    )
    useEffect(() => {
        if(window.innerWidth < 1201){
            setanimate({x:"0", opacity: 0})
        }
        else{
            setanimate({
                x:0,
                opacity: 1, 
                transition:{ duration:".5", transitionTimingFunction: "easeOutBack"}}
            )
        }
    }, [])
    function handleSlide(){
        setstate(!state)
        const silde = document.getElementById("Slide")

        if(state){
            console.log(state, " true")
            silde.style.height = "100vh"
            silde.style.borderBottomRightRadius = "0"
            silde.style.borderBottomLeftRadius = "0"
            
        } else{
            console.log(state, " false")
            silde.style.height = "0"
            silde.style.borderBottomRightRadius = "50%"
            silde.style.borderBottomLeftRadius = "50%"
            
        }
    }
    function menu(){
        const bur1 = document.querySelector(".bur1");
        const bur2 = document.querySelector(".bur2");
        const bur3 = document.querySelector(".bur3");

        if ( flipFlop ) {
            bur1.style.transform= "translate( -50% , -50% ) rotate(45deg)";
            bur2.style.opacity= "0";
            bur3.style.transform= "translate( -50% , -50% ) rotate(-45deg)";
            setflipFlop(false)
        } else {
            bur1.style.transform= "translate( -50% , -12px )  rotate(0deg)";
            bur2.style.transform= "translate( -50% , -50% )  rotate(0deg)";
            bur2.style.opacity= "1";
            bur3.style.transform= "translate( -50% , 10px ) rotate(0deg)";
            setflipFlop(true)
        }
    }
    
    function clickHandler(id){
        // console.log(clicks)
        let clone = clicks
        for(let i in clicks){
            clone[i].statues=false
        }
        clone[id].statues=true
        setClicks(clone)
        // console.log(clicks)

        handleScroll()

    }
    function handleScroll(){
        console.log("ddsdsdfsdfsd")
        var supportPageOffset = window.pageXOffset !== undefined;
        var isCSS1Compat = ((document.compatMode || "") === "CSS1Compat");

        // const All = document.getElementById("All")
        var y = supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop;
        console.log('y :',y)
        setTop(y)

        const one = all / 6
        const prog = document.getElementById("Progress")

        if(y < one * 1){
            console.log("one")
            // prog.style.height = `16.6%`
            progr(`16.6%`)
        } else if(y > one * 1 && y < one * 2){
            console.log("two")
            // prog.style.height = `33.33%`
            progr(`33.33%`)
        } else if(y > one * 2 && y < one * 3){
            console.log("three")
            // prog.style.height = `49%`
            progr(`49%`)
        }else if(y > one * 3 && y < one * 4){
            console.log("four")
            // prog.style.height = `66.66%`
            progr(`66.66%`)
        } else if(y > one * 4 && y < one * 5){
            console.log("five")
            // prog.style.height = `83%`
            progr(`83%`)
        }else if(y > one * 5 && y < one * 6){
            console.log("six")
            // prog.style.height = `100%`
            progr(`100%`)
        } 
    }
    function progr(value) {
        const prog = document.getElementById("Progress")
        if(document.querySelector("#All").offsetWidth < 1201){
            console.log("mobile")
            prog.style.width = value
            prog.style.height = "2px"
        }
        else {
            console.log("lap")
            prog.style.height = value
            prog.style.width = "2px"
        }
    }

    let navAnim = {
        initial: {
            x:"200px", opacity: 0
        },
        animate: {
            x:0,
            opacity: 1, 
            transition:{ duration:".5", transitionTimingFunction: "easeOutBack"}
        }
    }
    return (
        <motion.div className="sub-nav" variants={navAnim} initial="initial" animate="animate" >
                <div className="progress" id="Progress"></div>
                <div className="navbar">
                <div className="logo">
                    <div className="in">
                        Sipua
                    </div>
                </div>
                <div className="slide" id="Slide">
                    <div className="tabs">
                        <ul>
                            {clicks.map(item => <li><Href key={item.id} id={item.id} href={item.href} clicked={clickHandler} statues={item.statues} /></li>)}
                        </ul>
                    </div>
                    <div className="links">
                        <Linkss />
                    </div>
                </div>
                <div onClick={handleSlide} className="more">
                    <div className="in">
                        <button onClick={menu} className="navbar-toggler"> 
                            <div className="bur1 bur1d bur"></div>
                            <div className="bur2 bur2d bur"></div>
                            <div className="bur3 bur3d bur"></div>
                        </button>
                    </div>
                </div>
            </div>
        </motion.div>
    )
}

export default Navbar


