import React, {useEffect} from 'react'
import { motion } from "framer-motion"

function Href({id, href, clicked, statues}) {
    let animate = {}
    useEffect(() => {
        if(statues){
            animate = {x:"-5px", y: "5px"}
        }else{
            animate = {x:"-150px", y: "5px"}
        }
    }, [])
    setTimeout(() => {
        animate = {x:"-150px", y: "5px"}
    }, 1000);
    return (
        <a href={`#${href}`} onClick={()=>clicked(id)}> <motion.span  initial={{x:0}} animate={{animate}}></motion.span> {href}</a>
    )
}

export default Href
